h1{
  font-family: Cambria Bold;
  font-size: 50px;
}
p{
  font-family: monospace;
  font-size: 17px;
}
li{
  margin: auto;
  font-size: 20px;
}
button{
  margin-top: 10px;
  border-radius: 10px;
  width: 150px;
  height: 40px;
}
button:hover{
  opacity: 80%;
  transition: opacity 1.0s;
}
hr{
  height:4px;
  border-width:0;
  color:black;
  background-color:black;
}
.text{
  width: 1000px;
  text-align: justify;

}
.Central{
  padding: 10px;
  background: hsla(63, 57%, 8%, 0.1);
}
.Cont1{
  width: 1000px;
  justify-content: center;
  display: flex;
  text-align: center;
  flex-wrap: wrap;
}
.Cont2{
  width: 1000px;
  justify-content: center;
  display: flex;
  text-align: center;
  flex-wrap: wrap;
}

img{
  width: 100%;
  height: 180px;
  border-radius: 12px;
}
.Problema1:hover{
  opacity: 80%;
  transition: opacity 1.0s;
}
.Problema1{
  background-color: rgb(170, 170, 170);
  opacity: 100%;
  transition: opacity 1.5s;
  margin: 10px;
  border: 3px solid #282828;
  width: 300px;
  height: 400px;
  border-radius: 15px;
  align-items: center;
}
.Problema2{
  background-color: rgb(170, 170, 170);
  opacity: 100%;
  transition: opacity 1.5s;
  margin: 10px;
  border: 3px solid #282828;
  width: 300px;
  height: 350px;
  border-radius: 15px;
  align-items: center;
}
.Problema1 > button {
  margin-top: 10px;
  border-radius: 10px;
  width: 100px;
  height: 40px;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(90deg, #22c1c3, #fdbb2d);  
}

